import { UserService } from './../../SERVICES/user.service';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { AngularFirestore } from '@angular/fire/firestore';
import { Component } from '@angular/core';

@Component({
  selector: 'app-bene-my-events',
  templateUrl: './bene-my-events.page.html',
  styleUrls: ['./bene-my-events.page.scss'],
})
export class BeneMyEventsPage  {
  items: Observable<any[]>;
  Collection: any; 
  expenseId: any;

  constructor(public firestore: AngularFirestore,public router: Router,private user: UserService,) {
    console.log(this.user.getUID())
  this.Collection = this.firestore.collection('/publicevents', ref => ref.where('participants', 'array-contains', this.user.getUID()))
  this.items = this.Collection.valueChanges();
  }

goTo(postID: string){
  this.router.navigate(['/bene-details/'+ postID])
} 


}
